export default {
	props: {
		title: String,
		arrayData: Array,
		initIndex: Number
	},
	data() {
		return {
			sIndex:this.$props.initIndex
		}
	},
	methods:{
		bindPickerChange(e) {
			this.sIndex = e.target.value;
			this.$emit('bindPickerChange',this.sIndex);
		}
	}
}
